// app/layout.tsx - OPTIMIZED VERSION

import type { Metadata } from "next";
import { Geist, Geist_Mono } from "next/font/google";
import "./globals.css";

// ðŸ§  Global Providers - Client Components
import { Providers } from "@/components/providers";

// ðŸ§± Shared Layout Components - Load immediately
import Header from "@/components/header";
import Footer from "@/components/footer";

// ðŸŽ¯ Lazy load the enrollment banner since it's not critical
import dynamic from "next/dynamic";

const EnrollmentBanner = dynamic(() => import("@/components/enrollment-banner"), {
  ssr: false, // Client-side only for better performance
  loading: () => null // No loading state needed for banner
});

// âœ… Toast System
import { ToastProvider } from "@/components/ui/toast";
import { Toaster } from "@/components/ui/toaster";

// Optimize font loading
const geistSans = Geist({
  variable: "--font-geist-sans",
  subsets: ["latin"],
  display: "swap", // Good - prevents font loading delays
  preload: true, // Preload the font
});

const geistMono = Geist_Mono({
  variable: "--font-geist-mono",
  subsets: ["latin"],
  display: "swap",
  preload: false, // Don't preload mono font if not critical
});

export const metadata: Metadata = {
  title: {
    default: "Kongoni Technical Vocational Training College",
    template: "%s | Kongoni Technical",
  },
  description: "Empowering trainees through digital innovation and technical excellence",
  keywords: ["technical training", "vocational education", "Kongoni", "technical college"],
  authors: [{ name: "Kongoni Technical" }],
  creator: "Kongoni Technical College",
  openGraph: {
    type: "website",
    locale: "en_US",
    url: "https://kongoni-technical.edu",
    title: "Kongoni Technical Vocational Training College",
    description: "Empowering trainees through digital innovation and technical excellence",
    siteName: "Kongoni Technical College",
  },
  twitter: {
    card: "summary_large_image",
    title: "Kongoni Technical Vocational Training College",
    description: "Empowering trainees through digital innovation and technical excellence",
  },
  robots: {
    index: true,
    follow: true,
    googleBot: {
      index: true,
      follow: true,
      "max-video-preview": -1,
      "max-image-preview": "large",
      "max-snippet": -1,
    },
  },
  // Add performance hints
  other: {
    'X-Content-Type-Options': 'nosniff',
  },
};

export default function RootLayout({
  children,
}: {
  children: React.ReactNode;
}) {
  return (
    <html lang="en" suppressHydrationWarning>
      <head>
        {/* Preload critical resources */}
        <link rel="preconnect" href="https://fonts.googleapis.com" />
        <link rel="preconnect" href="https://fonts.gstatic.com" crossOrigin="anonymous" />
        {/* Add DNS prefetch for any external resources */}
        <link rel="dns-prefetch" href="//images.unsplash.com" />
      </head>
      <body
        className={`antialiased bg-white text-gray-900 min-h-screen flex flex-col ${geistSans.variable} ${geistMono.variable}`}
      >
        <ToastProvider>
          <Providers>
            {/* Header loads immediately - critical for navigation */}
            <Header />
            
            {/* Main content */}
            <main className="flex-1 px-4 py-6">{children}</main>
            
            {/* Footer loads immediately */}
            <Footer />
            
            {/* Toast system */}
            <Toaster />
            
            {/* ðŸŽ¯ Enrollment Banner - Lazy loaded, non-critical */}
            <EnrollmentBanner />
          </Providers>
        </ToastProvider>
      </body>
    </html>
  );
}